import "./contacts.scss";
import { useForm } from 'react-hook-form';

function Contacts() {
  const { register, formState: { errors }, handleSubmit, reset } = useForm({ mode: "onBlur" });

  const onSubmit = (formData) => {
    console.log(formData)
    reset();
  }
  return (

    <section className="contacts" id="contacts">

      <div className="contacts__inner">
        <form onSubmit={handleSubmit(onSubmit)}>
          <label>
            Your First Name:   <br />
            <input
              {...register("firstName", {
                required: "Filed can`t be empty",
                minLength: {
                  value: 3,
                  message: "Minimum 3 chars"
                }
              })} placeholder="Your Name" />
          </label>
          <p>
            {errors?.firstName && (
              <span>{errors?.firstName.message || "Error!"}</span>
            )}
          </p>
          <label>
            Your Second Name:   <br />
            <input  {...register("secondName", {
              required: "Filed can`t be empty",
              minLength: {
                value: 3,
                message: "Minimum 3 chars"
              }
            })}
              placeholder="Your Second Name" />
          </label>
          <p>
            {errors?.secondName && (
              <span>{errors?.secondName.message || "Error!"}</span>
            )}
          </p>
          <label>
            Your Email:<br />
            <input {...register("email", {
              required: "Filed can`t be empty",
              pattern: {
                value: /^((([0-9A-Za-z]{1}[-0-9A-z.]{1,}[0-9A-Za-z]{1})|([0-9А-Яа-я]{1}[-0-9А-я.]{1,}[0-9А-Яа-я]{1}))@([-A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u,
                message: "Invalid email address",
              }
            })}
              placeholder="Your Email" />
          </label>
          <p>
            {errors?.email && (
              <span>{errors?.email.message || "Error!"}</span>
            )}
          </p>
          <button type="submit">Submit</button>
        </form>
      </div>
    </section>
  )
}

export default Contacts;